import React from 'react';


function Main (props) {
    const {products , onAdd} = props;

    return(
    <maincontent className="maincontent">
            <div>
                <h2>商品欄</h2>
            </div>
        <div className="commodity">
           {products.map((product)=> {
               return (
               <div className="images" key={product.id}>
                    <img  className="img" src={product.image} alt={product.name}></img>
                    <h3>{product.name}</h3>
                    <p>${product.price}</p>
                    <button onClick={()=>onAdd(product)}>加入購物車</button>
               </div>
               )
            })}
        </div>
    </maincontent>
    );
};

export default Main;