import React from 'react';

function Checkout (props) {
    const {cartItems , onAdd ,onRemove , onReset} = props ;
    const totalPrice = cartItems.reduce((a,c) => a + c.price * c.qty,0);
    return(
        <checkoutcontent className="checkoutcontent">
            <h2>結帳欄</h2>
            <div>
                {cartItems.length ===0 && <div>購物車還是空的喔，趕緊去挑選商品吧！</div>}
                {cartItems.map((item)=>(
                    <div key={item.id} className="row">
                        <div className='checkoutMain'>{item.name}</div>
                        <div className='checkoutMain'>
                            <button onClick={()=>onAdd(item)} className='add'>+</button>
                            <button onClick={()=>onRemove(item)} className='remove'>-</button>
                        </div>
                        <div className='text-right checkout'>
                            {item.qty} x ${item.price}
                            <button onClick={()=>onReset(item)} className='badge'>x</button>
                        </div>
                    </div>
                ))}
            </div>
            {cartItems.length !==0 &&(
                <>
                <hr></hr>
                <div className='row'>
                    <div>
                    總金額
                    </div>
                    ${totalPrice}
                    </div>
                    <hr></hr>
                    <div>
                        <button onClick={()=>alert(`結帳完成 結帳金額為${totalPrice}`)}>結帳</button>
                    </div>
                </>
            )}
        </checkoutcontent>
    );
};


export default Checkout;